@import "../settings/settings";

@import "../tools/medias-queries/all";

.hero {
 &.has-background {
 min-height: 65vh;
 display: flex;

 &.is-mini {
 min-height: 35vh;
 }

 .hero-body {
 display: flex;
 justify-content: center;
 flex-direction: column;
 }

 &.bg-home {
 background: url("/old/assets/images/background-home.webp") no-repeat center
 center;
 background-size: cover;

 .hero-body {
 background-color: rgba($senary, 0.7);
 }
 }

 &.bg-about-me {
 background: url("/old/assets/images/background-about-me.webp") no-repeat
 center center;
 background-size: cover;

 .hero-body {
 background-color: rgba($secondary, 0.7);
 }
 }

 &.bg-mentorship {
 background: url("/old/assets/images/background-mentorship.webp") no-repeat
 center center;
 background-size: cover;

 .hero-body {
 background-color: rgba($accent, 0.7);
 }
 }

 &.bg-posts {
 background: url("/old/assets/images/background-posts.webp") no-repeat center
 center;
 background-size: cover;

 .hero-body {
 background-color: rgba($quaternary, 0.7);
 }
 }

 &.bg-projects {
 background: url("/old/assets/images/background-projects.webp") no-repeat
 center center;
 background-size: cover;

 .hero-body {
 background-color: rgba($quinary, 0.7);
 }
 }
 }
}
