@import "../settings/settings";

@import "../tools/medias-queries/all";

.navbar {
 &.is-active {
 @include for-tablet-landscape-and-down {
 position: fixed;
 height: 100vh;
 width: 100vw;
 overflow: hidden;
 }
 }

 .navbar-burger {
 background: transparent;
 border: 1px solid transparent;
 }

 .navbar-menu {
 @include for-tablet-landscape-and-down {
 padding-bottom: 0;
 }

 &.is-active {
 @include for-tablet-landscape-and-down {
 background-color: $tertiary;

 position: inherit;
 height: 100vh;
 width: 100vw;

 display: flex;
 flex-direction: column;
 }

 .navbar-start {
 @include for-tablet-landscape-and-down {
 }
 }

 .navbar-end {
 @include for-tablet-landscape-and-down {
 align-self: flex-end;
 justify-content: flex-end;
 display: flex;
 height: 100%;
 align-items: flex-end;
 }
 }
 }

 .navbar-item {
 &.is-active {
 @include for-tablet-landscape-and-down {
 background-color: rgba(223, 237, 248, 0.1);
 }
 }
 }
 }
}
