@import "settings/settings";

.button {
 &.has-icon {
 width: 50px;
 height: 50px;

 border-width: 2px;

 transform: rotateX(0deg);

 transition-property: transform;
 transition-duration: 1s;

 &.is-flipped {
 transform: rotateX(180deg);
 }

 &:hover,
 &:active,
 &:focus {
 .icon i {
 color: $white !important;
 }
 }
 }
}

/* The typing effect */
@keyframes typing {
 from {
 transform: rotateX(0deg);
 }
 to {
 transform: rotateX(180deg);
 }
}
