@import "../settings/settings";

@import "../tools/truncate";
@import "../tools/opacity-animate";
@import "../tools/medias-queries/all";
@import "../tools/is-even";
@import "../tools/not-last";

@import "../generics/vertical-center-container";
@import "../generics/horizontal-center-container";

.post {
 margin: 27px 0;

 @include for-tablet-portrait-and-down {
 margin: 10px 0;
 }

 .post-content {
 font-size: 1rem;

 @include for-tablet-landscape-up {
 font-weight: 500;

 h2,
 h3,
 h4,
 h5,
 h6 {
 padding-top: 0.5rem;
 margin-top: 0;
 }

 h2,
 h3 {
 padding-top: 0.8rem;
 }
 }
 }

 .level {
 padding-bottom: 0.5rem;
 }

 .post-header,
 .post-content {
 margin: 1% 15% 2%;

 @include for-tablet-landscape-and-down {
 margin: 1% 5% 2%;
 }

 @include for-tablet-portrait-and-down {
 margin: 5px 15px 10px;
 }

 .title {
 color: $quinary;

 @include for-tablet-portrait-and-down {
 font-size: 1.5rem;
 }
 }

 .subtitle {
 color: rgba($senary, 0.75);

 @include for-tablet-portrait-and-down {
 font-size: 1rem;
 }
 }
 }

 .post-cover-image {
 max-height: 500px;
 width: 100%;

 padding: 0;
 }

 blockquote {
 font-size: medium !important;
 }
}
